.current {
    color: green;  // 将当前链接设为绿色
  }

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  padding: 0.5rem 2rem;
  font-family: 'PingFang SC', 'Microsoft YaHei', Arial, sans-serif;
}

.navbar-logo {
  display: flex;
  align-items: center;
}

.logo-img {
  height: 40px;
  width: auto;
  border-radius: 50%;
}

@import url('https://fonts.googleapis.com/css2?family=Ma+Shan+Zheng&display=swap');

.dunhuang-font {
  font-family: 'Ma Shan Zheng', cursive;
}

.logo-text {
  font-size: 2rem;
  font-weight: bold;
  color: #e6a4a4;  // 玫瑰金色
  text-decoration: none;
  background: linear-gradient(45deg, #e6a4a4, #d4a5a5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: pulse 2s infinite;
  position: relative;
}

.logo-text::before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  border: 2px solid #e6a4a4;
  border-radius: 50%;
  animation: rotate 4s linear infinite;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 0%, 10% 10%, 90% 10%, 90% 90%, 10% 90%, 10% 10%);
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.navbar-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.navbar-links a {
  color: #333;
  text-decoration: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: background 0.2s, color 0.2s;
}

.navbar-links a.current,
.navbar-links a:hover {
  background: #2d8cf0;
  color: #fff;
}

.search-form {
  display: flex;
  align-items: center;
  background: #f5f5f5;
  border-radius: 4px;
  padding: 2px 6px;
}

.search-input {
  border: none;
  background: transparent;
  outline: none;
  padding: 4px 8px;
  font-size: 1rem;
}

.search-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  color: #2d8cf0;
  padding: 0 4px;
}

.nav-icon {
  margin-right: 0.5rem;
  font-size: 1.2rem;
}